.postTitle {
  font-size: 40px;
  font-weight: 500;
}
.postMeta {
  font-weight: normal;
  color: rgba(0, 0, 0, 0.54);
  font-size: 20px;
}
.author {
  margin: 30px 0;
  display: flex;
  align-items: center;
}
.author img {
  width: 60px;
  height: 60px;
  border-radius: 25px;
}
.author a {
  text-decoration: none;
  color: black;
  font-size: 16px;
  margin-right: 10px;
}
.publishedDate {
  color: rgba(0, 0, 0, 0.44);
  font-size: 14px;
}
.author p {
  margin: 0;
  padding-top: 5px;
}
.postContent {
  width: 100%;
}
